<template>
    <div v-if="FileData.length>0">
        <div class="col-md-12 p-0 mb-2" style="z-index:99999999;">
            <div class="card-header py-0">
                <span class="p-name imgname"><em :class="GetClass(extension)"></em>{{ this.fileName }}</span>
                <a class="float-right font-20 mt-2 text-white" v-if="extension != '.pdf'" @click="DownloadFile"><em
                        class="fa fa-download"></em></a>&nbsp;
            </div>
        </div>
        <div class="col-md-12 p-0 m-auto text-center"
            v-if="extension == '.docx' || extension == '.doc' || extension == '.xls' || extension == '.xlsx' || extension=='.pptx'">
            <iframe height="850px" width="100%" style="background-color:white"
                :src="'https://view.officeapps.live.com/op/embed.aspx?src=' + imgpath + ''" frameborder="0"></iframe>
        </div>
        <div class="col-md-12 p-0 m-auto text-center" v-if="extension == '.txt' || extension == '.pdf'">
            <iframe height="850px" width="100%" style="background-color:white" :src=" imgpath" frameborder="0"></iframe>
        </div>
        <div class="col-md-12 p-0 m-auto text-center"  v-if="extension == '.mp4'">
            <video   autoplay  controls id="backgroundvid">
                <source :src="imgpath" type="video/mp4">
            </video>
                                                                        
        </div>
        <div class="col-md-12 p-0 m-auto text-center"  v-if="extension == '.mp3'">
            <audio   autoplay  controls id="backgroundvid">
                <source :src="imgpath" type="audio/mpeg">
            </audio>
                                                                        
        </div>
        <div class="col-md-12 p-0 m-auto text-center"  v-if="extension == '.msg' || extension == '.otf' || extension == '.css'">
            <h2>No preview avaliable for this file.</h2>                                                                     
        </div>
        <div>
        <VueEasyLightbox v-if="extension == '.jpg' || extension == '.jpeg' || extension == '.png'" :visible="visibleRef"
            :imgs="imgpath">
        </VueEasyLightbox>
    </div>
</div>
<div v-else>
    <div class="col-md-12 p-0 m-auto text-center">
        <title>Link Expired</title>
        <body>
            <div class="content">
                <h1>Link Expired</h1>
                <p>We're sorry, but the link you are trying to access has expired.</p>
            </div>
         </body>                                  
    </div>
</div>
</template>
<script>
import DataService from '../../services/DataService';
import VueEasyLightbox from 'vue-easy-lightbox'
export default {
    components:{
        VueEasyLightbox
    },
    created: function () {
        this.GetStorageFileSummary();
        this.openLightbox();
    },
    data() {
        return {
            fileName: '',
            storageUniqueCode: null,
            visibleRef: false,
            fileType: null,
            path: null,
            filePath: null,
            extension: null,
            FileData:[],
        }
    },
    methods: {
        GetStorageFileSummary() {
            debugger
            this.isLoading = true;
            var decodeId =atob(window.location.search.split("=")[1].split("&")[0].replace(/[^a-z0-9\s]/gi, '').replace('3',''));
            var StorageFileId = parseInt(decodeId);
            var params = `StorageFileId=${StorageFileId}&refCode=PublicLink`;
            var vm = this;
            DataService.GetStorageFileSummary(params).then((response) => {
                vm.FileData=JSON.parse(response.data).FileJsonData;
                if(vm.FileData.length>0){
                    vm.storageUniqueCode = JSON.parse(response.data).FileJsonData[0].StorageUniqueCode
                    vm.filePath = JSON.parse(response.data).FileJsonData[0].storage_file_path
                    vm.fileName = JSON.parse(response.data).FileJsonData[0].storage_file_name
                    vm.fileType = JSON.parse(response.data).FileJsonData[0].storage_file_type
                    vm.extension = JSON.parse(response.data).FileJsonData[0].storage_file_ext
                    vm.imgpath = JSON.parse(response.data).FileJsonData[0].storage_file_path.replace("/wwwroot", "");
                }
            })
        },
        DownloadFile() {
            var vm = this;
            var params = `storageUniqueCode=${this.storageUniqueCode}`
            DataService.GetPublicallyStorageDoc(params).then((response) => {
                var blob = new Blob([response.data], { type: vm.fileType });
                var link = document.createElement('a');
                link.href = window.URL.createObjectURL(blob);
                var fileName = vm.fileName;
                link.download = fileName;
                link.click();
            })
        },
        GetClass: function(ext){
            var cl=null;
            if(ext=='.pdf'){
                cl='fa fa-file-pdf-o mr-2 text-danger'
            }
            else if(ext=='.xls' || ext=='.xlsx')
            {
                cl='fa fa-file-excel-o mr-2 text-danger'
            }
            else if(ext=='.png' || ext=='.jpeg' || ext=='.jpg')
            {
                cl='fa fa-file-image-o mr-2 text-danger'
            }
            else if(ext=='.txt')
            {
                cl='fa fa-file-text-o mr-2 text-danger'
            }
            else if(ext=='.doc' || ext=='.docx' || ext=='.txt')
            {
                cl='fa fa-file-word-o mr-2 text-danger'
            }
            else if(ext=='.mp4')
            {
                cl='fa fa-file-video-o mr-2 text-danger'
            }
            else if(ext=='.mp3')
            {
                cl='fa fa-file-audio-o mr-2 text-danger'
            }
            else{
                 cl='fa fa-file-o mr-2 text-danger'
            }
            return cl;
        },
        openLightbox: function () {
            var vm = this;
            vm.visibleRef = true
        },
        onHide: function () {
            var vm = this;
            vm.visibleRef = false
        },
    }
}
</script>
<style>
/* Don't copy this css in Css File - Harjit 21April23 */
body {
    padding: 0px;
    background: #1c1c1c;
    color: #f2f2f2;
}

footer {
    display: none;
}

.imgname {
    font-size: 16px;
}

.btn__close {
    display: none;
}

.card-header {
    border-bottom: 1px solid rgb(0 0 0);
}
footer{display: none;}
header{display: none;}
#divjuda_menu{display: none;}
.vel-modal {
    background: none !important;
}
body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}
.content {
    text-align: center;
}</style>
